.home__content {
    grid-template-columns: 116px repeat(2, 1fr);
    padding-top: 5.5rem;
    column-gap: 2rem;
    align-items: center;
}

.home__social {
    display: grid;
    grid-template-columns: max-content;
    row-gap: 1rem;
}


.home__scroll {
	margin-left: 9rem;
}

.wheel {
	animation: scroll 2s ease infinite;
}

@keyframes scroll {
	0% {
		transform: translateY(0);
	}

	30% {
		transform: translateY(3.75rem);
	}
}

.home__container {
	row-gap: 7rem;
}

.home__social-icon {
    font-size: 1.25rem;
    color: var(--title-color);
}

.home__social-icon:hover {
    color: var(--title-color-dark);
}

.home__title {
    font-size: 2rem;
    margin-bottom: var(--mb-0-25);
}

.home__subtitle {
    position: relative;
    font-size: var(--h3-font-size);
    padding-left: 5.4rem;
    font-weight: var(--font-normal);
    margin-bottom: var(--mb-0-25);
}

.home__subtitle::before {
    content: '';
    position: absolute;
    width: 70px;
    height: 1px;
    background-color: var(--text-color);
    left: 0;
    top: 1rem;

}

.home__description {
    max-width: 400px;
    margin-bottom: var(--mb-3);
}

.home__img {
    background: url(../../assets/profile_left.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    box-shadow: inset 0 0 0 9px rgb(255, 255, 255 / 30%);
    order: 1;
    justify-content: center;
    width: 400px;
    height: 400px;
    animation: updown 3s linear infinite;
}


@keyframes updown {
    0% {
        transform: translateY(-20px);
    }

    50% {
        transform: translateY(20px);
    }

    100% {
        transform: translateY(-20px);
    }
}


ul.leaf-wrap {
	list-style: none;
	position: absolute;
	height: 50px;
	width: 100%;
	margin-top: -200px;
	z-index: 0;

	animation: 1.5s ease-in 0s 1 fade-in;
}

@keyframes fade-in {
	0% {
		left: -50px;
		opacity: 0;
	}
	100% {
		left: 0;
		opacity: 1;
	}
}

.leaf {
	position: absolute;
	z-index: 0;
	width: 50px;
	height: 50px;
	border-radius: 75% 75% 0% 75%;
	background: linear-gradient(to right, #77ed9e, #53ad71);
	box-shadow: 8px 10px 20px -10px #3a5d46, 2px -1px 0 1px #3f8456,
		200px -100px 15px -15px #77ed9e, 200px -100px 0 -10px #3f8456,
		-140px 65px 15px -20px #77ed9e, -140px 65px 0px -13px #3f8456,
		140px 65px 0px -15px #77ed9e;
}

.leaf::after {
	content: "";
	display: block;
	width: 12px;
	height: 40px;
	border: 4px solid #3a9659;
	border-bottom: none;
	border-right: none;
	border-top: none;
	border-radius: 100% 100% 100% 10%;
	transform: rotate(120deg);
	position: absolute;
	top: -3px;
	left: 5px;
}

.leaf::before {
	content: "";
	display: block;
	width: 3px;
	height: 3px;
	background: none;
	border-radius: 100% 100% 100% 10%;
	transform: rotate(0deg);
	position: absolute;
	z-index: 1;
	top: 0px;
	left: 0px;
	box-shadow: 10px 15px 0 1px #3a9659, 10px 18px 0 1px #53ad71,
		20px 11px 0 1px #3a9659, 23px 11px 0 1px #53ad71, 20px 20px 0 1px #3a9659;
}

.leaf:nth-child(1) {
	animation: 3s ease-in 0s infinite leaf-1;
}

@keyframes leaf-1 {
	0% {
		left: 150px;
		transform: scale3d(1, 1, 1);
	}
	75% {
		transform: scale3d(1, 0.75, 1) translate(100px, 100px);
	}
	100% {
		left: 100%;
		transform: scale3d(1, 1, 1) translate(20px, -20px) rotate(-50deg);
	}
}


.leaf:nth-child(2) {
	animation: 2.5s ease-in 0.5s infinite leaf-2;
}

@keyframes leaf-2 {
	0% {
		left: 250px;
		transform: scale3d(0.5, 0.5, 0.5);
	}
	60% {
		transform: scale3d(0.75, 0.25, 0.5) translate(100px, -150px);
	}
	100% {
		left: 100%;
		transform: scale3d(0.5, 0.5, 0.5) translate(0px, -20px);
	}
}

.leaf:nth-child(3) {
	animation: 3.5s ease-in 0.75s infinite leaf-3;
}

@keyframes leaf-3 {
	0% {
		left: 150px;
		transform: scale3d(1, 0.5, 0.5);
	}
	30% {
		transform: scale3d(0.25, 0.65, 0.5) translate(100px, 100px) rotate(-30deg);
	}
	100% {
		left: 100%;
		transform: scale3d(1, 0.5, 0.5) translate(100px, -200px);
	}
}

.leaf:nth-child(4) {
	animation: 4.5s ease-in 0s infinite leaf-4;
}

@keyframes leaf-4 {
	0% {
		left: 250px;
		transform: rotate(0deg) scale(0.6);
	}
	20% {
		transform: rotate(5deg) translate(0, 50px);
	}
	100% {
		left: 100%;
		transform: rotate(90deg) translate(0px, -400px) scale(1);
	}
}


.leaf:nth-child(5) {
	animation: 5.5s ease-in 0.5s infinite leaf-5;
}

@keyframes leaf-5 {
	0% {
		left: 250px;
		transform: rotate(0deg) scale(0.5);
	}
	20% {
		transform: translate(300px, 100px) rotate(90deg) scale(0.6);
	}
	45% {
		transform: translate(300px, 0px) rotate(0deg) scale(0.85);
	}
	70% {
		transform: translate(300px, 250px) rotate(-180deg) scale(0.6);
	}
	100% {
		left: 100%;
		transform: rotate(-200deg) translate(-100px, -200px) scale(1);
	}
}

.home__scroll-name {
	color: var(--title-color);
	font-weight: var(--font-medium);
	margin-right: var(--mb-0-25);
	margin-left: 2px;
}

@media screen and (max-width: 768px) {
	body {
	  margin: 0 0 var(--header-height) 0;
	}
  
	.section {
	  padding: 2rem 0 4rem;
	}
  
	.section__subtitle {
	  margin-bottom: var(--mb-3);
	}
  }
  
@media screen and (max-width: 992px) {
	.home__content {
		grid-template-columns: 100px repeat(2, 1fr);
		column-gap: 1.25rem;
	}

	.home__subtitle {
		padding-left: 3.75rem;
		margin-bottom: var(--mb-1);
	}

	.home__subtitle::before {
		width: 42px;
		top: 0.8rem
	}

	.home__description {
		max-width: initial;
		margin-bottom: var(--mb-2-5);
	}

	.home__img {
		width: 250px;
		height: 250px;
		box-shadow: inset 0 0 0 9px rgb(255, 255, 255 / 30%);
	}

	.home__scroll {
		margin-left: 7.5rem;
	}

}
  
  /* For small devices */
@media screen and (max-width: 768px) {
	.home__content {
		grid-template-columns: 0.5fr 3fr;
		padding-top: 3.5rem;
	}

	.home__img {
		order: initial;
		justify-self: initial;
	}

	.home__data {
		grid-column: 1/3;
	}

	.home__img {
		box-shadow: inset 0 0 0 6px rgb(255, 255, 255 / 30%);
		width: 200px;
		height: 200px;
	}
	
	.home__scroll {
		display: none;
	}
  }

@media screen and (max-width: 350px) {
	.home__img {
		width: 180px;
		height: 180px;
	}

  }