.post_list__container {
    text-align: left;
    width: 66.67%;
    margin: 0 auto;
    border: 1px solid #000;
    padding: 10px;
    margin-bottom: 2rem;
    background-color: var(--container-color);
    border-radius: 1.25rem;
}

.post_link {
    font-weight: var(--font-medium);
    color: var(--title-color);;
}

.tag__container {
    display: flex;
    align-items: center;
}

#h5_tags {
    color: var(--title-color);
}

